
.vue-drawer {
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /*overflow: hidden;*/
  /*max-width: 600px;*/
}
.vue-drawer > .main {
  /*max-width: 600px;*/
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  transition: visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  transition: transform ease-in-out 0.38s, visibility 0.38s;
  transition: transform ease-in-out 0.38s, visibility 0.38s, -webkit-transform ease-in-out 0.38s;
}
.vue-drawer > .main > .mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity ease-in-out 0.38s, visibility ease-in-out 0.38s;
  transition: opacity ease-in-out 0.38s, visibility ease-in-out 0.38s;
  background-color: rgba(0, 0, 0, 0.3);
}
.vue-drawer > .main > .active {
  visibility: visible;
  opacity: 1;
}
.vue-drawer > .drawer {
  max-width: 15rem;
  background-color: #fff;
  position: absolute;
  top: 0;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
  visibility: hidden;
  -webkit-transition: visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  transition: visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  transition: transform ease-in-out 0.38s, visibility 0.38s;
  transition: transform ease-in-out 0.38s, visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  will-change: none;
}
.vue-drawer > .drawer-left {
  left: 0;
  -webkit-transform: translateX(-102%);
          transform: translateX(-102%);
}
.vue-drawer > .drawer-right {
  right: 0;
  -webkit-transform: translateX(102%);
          transform: translateX(102%);
}
.vue-drawer > .active {
  pointer-events: inherit;
  visibility: visible;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}



